<script>
  import { onMount } from "svelte";
  import { fade, scale } from "svelte/transition";
  import { data, page, setting, Explain } from "../store";
  import collection from "../data/collection.json";
  import equipment from "../data/equipment.json";
  import souvenir from "../data/souvenir.json";
  import spellcard from "../data/spellcard.json";
  import role from "../data/role.json";
  import growth from "../data/growth";

  export const menu = menuHandle;
  let enable = false;
  let index = "collection";
  let state = stateHandle();
  let explain;

  onMount((_) => (explain = $Explain));

  const position = [
    {
      x: 50,
      y: 0,
    },
    {
      x: 35.4,
      y: -35.4,
    },
    {
      x: 0,
      y: -50,
    },
    {
      x: -35.4,
      y: -35.4,
    },
    {
      x: -50,
      y: 0,
    },
    {
      x: -35.4,
      y: 35.4,
    },
    {
      x: 0,
      y: 50,
    },
    {
      x: 35.4,
      y: 35.4,
    },
  ];

  function stateHandle() {
    let State = growth.role[$data.role]($data.lv);
    let chance = {
      blue: "普通",
      green: "稀有",
      red: "传奇",
    };
    return [
      {
        type: "maxHealth",
        name: "最大生命值",
        detail: "显而易见，生命值不会超过这个数。\n只有等级才能让这个数字提升。",
        data: State.health,
      },
      {
        type: "attack",
        name: "攻击力",
        detail: "所有攻击都以这个数为基础。不过也有例外。\n只有等级才能让这个数字提升。",
        data: State.attack,
      },
      {
        type: "speed",
        name: "速度",
        detail:
          "决定了回合中行动的自由度，速度快，能做的事也就会更多。\n抽牌数=基础抽牌数+(速度-敌人平均速度)，小数点后面部分就是多抽一张的概率。\n手牌上限=抽牌数+速度四舍五入。",
        data: State.speed,
      },
      {
        type: "power",
        name: "灵力",
        detail:
          "牌左上角的数字就是其灵力消耗。灵力每回合初都会回复至上限。\n额外灵力：等同于灵力，优先消耗，上限等同于牌库总牌数。",
        data: State.power,
      },
      {
        type: "health",
        name: "生命值",
        detail: "必须不让它归零才行。",
        data: $data.health,
      },
      {
        type: "sugar",
        name: "糖",
        detail: "面临选择时可用选项的数量。",
        data: $data.sugar,
      },
      {
        type: "level",
        name: "等级",
        detail: "等级越高越强大。\n只能通过战斗提升等级。",
        data: $data.lv,
      },
      {
        type: "chance",
        name: "机会",
        detail: `剩余${
          $data.chance.amount
        }次战败时回到战斗前的机会。机会消耗时，回复生命值并获得一件${
          chance[$data.chance.type]
        }收藏品。`,
        data: $data.chance.amount,
      },
    ];
  }
  function menuHandle(e) {
    state = stateHandle();
    e.preventDefault();
    if ($page != "Index") enable = !enable;
  }
</script>

<div class="body {enable && 'menu'} {$data.role}">
  <div class="info" id="info">
    <div class="title">
      <icon class="icon-data"></icon>
      <txt>思与想的境界</txt>
    </div>
  </div>
  <div class="main">
    <div class="left">
      <txt>(=￣ ρ￣=) ..zzZZ</txt>
      <div class="state">
        {#each state as s, i (i)}
          <div
            on:mouseenter={(e) => explain.show(e)}
            on:mousemove={(e) =>
              explain.move(
                e,
                s,
                s.type == "chance" ? $data.chance.type : "blue",
              )}
            on:mouseleave={(_) => explain.hide()}
          >
            <icon
              style="left:{150 + position[i].x * 1.5 - 16}px;top:{150 +
                position[i].y * 1.5 -
                16}px;"
              class="icon-{s.type}"
            ></icon>
            <txt
              style="left:{150 + position[i].x * 2.3 - 25}px;top:{150 +
                position[i].y * 2.3 -
                16}px;">{s.data}</txt
            >
          </div>
        {/each}
        <img
          src="/img/role/{$setting.resource}/{$data.role}.webp"
          on:mouseenter={(e) => explain.show(e)}
          on:mousemove={(e) =>
            explain.move(
              e,
              {
                name: role[$data.role].name,
                detail: role[$data.role].talent,
              },
              "blue",
            )}
          on:mouseleave={(_) => explain.hide()}
        />
      </div>
      <div
        class="tip"
        style="--width:200px"
        on:mouseenter={(e) => explain.show(e)}
        on:mousemove={(e) =>
          explain.move(
            e,
            {
              name: "硬币",
              detail: "对，这是钱。",
            },
            "gold",
          )}
        on:mouseleave={(_) => explain.hide()}
      >
        <icon class="coin icon-coin"></icon>
        <txt>{$data.coin}</txt>
      </div>
      <div
        class="tip action"
        style="--width:250px"
        on:click={(_) => {
          //$page = "Index";
          enable = false;
        }}
      >
        保存并回到标题界面
      </div>
    </div>
    <div class="right">
      <div class="item">
        <div class="index">
          <txt
            class={index == "collection" && "selected"}
            on:click={(_) => (index = "collection")}>收藏品</txt
          >
          <txt
            class={index == "spellcards" && "selected"}
            on:click={(_) => (index = "spellcards")}>牌库</txt
          >
          <txt
            class={index == "consumable" && "selected"}
            on:click={(_) => (index = "consumable")}>消耗品</txt
          >
        </div>
        <div class="bookmark" />
        <div class="bg" />
        {#if index == "collection"}
          <div
            class="collection"
            in:fade={{ duration: 200 }}
            out:fade={{ duration: 200 }}
          >
            {#each Object.keys($data.collection) as c, i (i)}
              <div
                on:mouseenter={(e) => explain.show(e)}
                on:mousemove={(e) =>
                  explain.move(e, collection[c], collection[c].type)}
                on:mouseleave={(_) => explain.hide()}
              >
                <icon class="icon-{c}"></icon>
                {#if Object.values($data.collection)[i] > 1}
                  <txt>{Object.values($data.collection)[i]}</txt>
                {/if}
              </div>
            {/each}
          </div>
        {/if}
        {#if index == "spellcards"}
          <div
            class="spellcards"
            in:fade={{ duration: 200 }}
            out:fade={{ duration: 200 }}
          >
            <!-- {#each Object.keys($data.spellcards) as s, i (i)}
              <div
                class="spellcard"
                on:mouseenter={(e) => explain.show(e)}
                on:mousemove={(e) => explain.move(e, spellcard[s], "blue")}
                on:mouseleave={(_) => explain.hide()}
              >
                <icon class="icon-{s}"></icon>
                {#if Object.values($data.spellcards)[i] > 1}
                  <txt>{Object.values($data.spellcards)[i]}</txt>
                {/if}
              </div>
            {/each} -->
          </div>
        {/if}
        {#if index == "consumable"}
          <div
            class="consumable"
            in:fade={{ duration: 200 }}
            out:fade={{ duration: 200 }}
          />
        {/if}
      </div>
      <div class="other">
        {#if $data.equipment}
          <div
            class="equipment"
            on:mouseenter={(e) => explain.show(e)}
            on:mousemove={(e) =>
              explain.move(e, equipment[$data.equipment], "gold")}
            on:mouseleave={(_) => explain.hide()}
          >
            <icon class="icon-{$data.equipment}"></icon>
            <txt>装备</txt>
          </div>
        {/if}
        {#if $data.souvenir}
          <div
            class="souvenir"
            on:mouseenter={(e) => explain.show(e)}
            on:mousemove={(e) =>
              explain.move(e, souvenir[$data.souvenir], "purple")}
            on:mouseleave={(_) => explain.hide()}
          >
            <icon class="icon-{$data.souvenir}"></icon>
            <txt>纪念品</txt>
          </div>
        {/if}
      </div>
    </div>
  </div>
</div>

<style>
  icon {
    color: white;
  }

  .body {
    position: absolute;
    right: 0;
    top: 0;
    z-index: -1;
    opacity: 0;
    backdrop-filter: blur(16px) saturate(150%) brightness(1.2);
    transition: 0.4s;
    flex-direction: row;
  }
  .menu {
    z-index: 8;
    opacity: 1;
  }

  .info {
    position: absolute;
    left: 0;
    top: 0;
  }
  .title {
    flex-direction: row;
    margin: 5px;
  }
  .title > icon {
    font-size: 70px;
    filter: drop-shadow(0 0 4px #666);
    color: white;
  }
  .title > txt {
    font-size: 32px;
    height: 70px;
    line-height: 70px;
    margin-left: 10px;
    font-family: title;
    color: white;
    text-shadow:
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666;
  }

  .main {
    flex-direction: row;
    align-items: center;
    border-radius: 12px;
    background-color: rgb(236 125 125);
    background-image: linear-gradient(
      135deg,
      rgba(255, 255, 255, 0.2) 25%,
      transparent 25%,
      transparent 50%,
      rgba(255, 255, 255, 0.2) 50%,
      rgba(255, 255, 255, 0.2) 75%,
      transparent 75%,
      transparent
    );
    box-shadow:
      0 0.9rem 1.7rem rgba(0, 0, 0, 0.25),
      0 0.7rem 0.7rem rgba(0, 0, 0, 0.22);
    height: 430px;
  }

  .left {
    align-items: center;
    justify-content: center;
    width: 300px;
    height: 430px;
    position: relative;
    overflow: hidden;
  }
  .right {
    align-items: flex-end;
    justify-content: space-between;
    margin-left: 5px;
    margin-right: 10px;
    height: 100%;
  }

  .left > txt {
    width: 250px;
    line-height: 40px;
    font-size: 16px;
    transform: rotate(-35deg);
    background-color: white;
    position: absolute;
    left: -70px;
    top: 25px;
    text-align: center;
    filter: drop-shadow(0 0 4px #888);
    color: #666;
  }
  .state {
    align-items: center;
    justify-content: center;
    width: 300px;
    height: 300px;
    position: relative;
  }
  .state > div > icon {
    font-size: 32px;
    position: absolute;
  }
  .state > div > txt {
    line-height: 32px;
    font-size: 18px;
    font-family: title;
    color: white;
    text-shadow:
      0 0 3px #8d7192,
      0 0 3px #8d7192,
      0 0 3px #8d7192,
      0 0 3px #8d7192,
      0 0 3px #8d7192,
      0 0 3px #8d7192,
      0 0 3px #8d7192;
    position: absolute;
    text-align: center;
    width: 50px;
    text-align: center;
  }
  .state > img {
    width: 90px;
    border-radius: 50%;
    border: 5px solid white;
    background-color: #bbb;
  }
  .tip {
    flex-direction: row;
    height: 45px;
    width: var(--width);
    align-items: center;
    justify-content: center;
    color: #888;
    font-family: remark;
    font-size: 20px;
    box-sizing: border-box;
    background-image: radial-gradient(
        circle at 1px 8px,
        transparent 6px,
        white 6px,
        white 0px
      ),
      radial-gradient(
        circle at calc(var(--width) / 2) 8px,
        transparent 6px,
        white 6px,
        white 0px
      );
    background-size: calc(var(--width) / 2) 15px;
    background-position:
      0 0,
      calc(var(--width) / 2) 0;
    background-repeat: no-repeat repeat;
    transition: 0.3s;
    cursor: pointer;
  }
  .tip:hover {
    filter: drop-shadow(0 0 6px #777);
  }
  .coin {
    font-size: 28px;
    color: #b896be;
    margin-right: 5px;
  }

  .item {
    height: 320px;
    width: 360px;
    position: relative;
  }
  .item > div {
    flex-direction: row;
  }
  .item > div::-webkit-scrollbar {
    display: none; /* Chrome Safari */
  }
  .index {
    position: absolute;
    top: -20px;
    left: 20px;
  }
  .index > txt {
    width: 80px;
    line-height: 40px;
    margin: 5px;
    background-image: radial-gradient(
      circle at 0 0,
      transparent 0,
      transparent 15px,
      #fff 15px
    );
    font-size: 20px;
    color: #888;
    text-align: center;
    box-sizing: border-box;
    cursor: pointer;
    font-family: normal;
    transition: 0.2s;
    font-weight: bold;
  }
  .index > txt.selected {
    transform: translateY(-7px);
    z-index: 1;
  }
  .index > txt:hover {
    transform: translateY(-7px);
  }
  .bg,
  .collection,
  .spellcards,
  .consumable {
    position: absolute;
    left: 0;
    top: 18px;
    height: 300px;
    width: 360px;
    flex-wrap: wrap;
    align-content: flex-start;
    overflow-x: hidden;
    overflow-y: auto;
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none; /* IE 10+ */
    padding: 12px;
    box-sizing: border-box;
  }
  .bg {
    filter: drop-shadow(0 0 6px #666);
    background-color: white;
    background-image: url("data:image/svg+xml,%3Csvg width='84' height='48' viewBox='0 0 84 48' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 0h12v6H0V0zm28 8h12v6H28V8zm14-8h12v6H42V0zm14 0h12v6H56V0zm0 8h12v6H56V8zM42 8h12v6H42V8zm0 16h12v6H42v-6zm14-8h12v6H56v-6zm14 0h12v6H70v-6zm0-16h12v6H70V0zM28 32h12v6H28v-6zM14 16h12v6H14v-6zM0 24h12v6H0v-6zm0 8h12v6H0v-6zm14 0h12v6H14v-6zm14 8h12v6H28v-6zm-14 0h12v6H14v-6zm28 0h12v6H42v-6zm14-8h12v6H56v-6zm0-8h12v6H56v-6zm14 8h12v6H70v-6zm0 8h12v6H70v-6zM14 24h12v6H14v-6zm14-8h12v6H28v-6zM14 8h12v6H14V8zM0 8h12v6H0V8z' fill='rgb(255, 220, 220)' fill-opacity='0.3' fill-rule='evenodd'/%3E%3C/svg%3E");
    background-size: 30%;
  }
  .bookmark {
    width: 30px;
    height: 100px;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 50% 75%, 0 100%);
    background-color: #b96161;
    position: absolute;
    bottom: -60px;
    left: 30px;
  }
  .spellcards icon {
    font-size: 80px;
    margin: auto;
  }
  .collection icon {
    margin: auto;
    font-size: 36px;
  }
  .collection > div {
    width: 48px;
    height: 48px;
    margin: 4px;
    background-color: rgb(183 88 88 / 70%);
    background-color: #b896be;
    border-radius: 50%;
    box-sizing: border-box;
    position: relative;
    cursor: pointer;
    transition: 0.2s;
  }
  .collection txt {
    font-size: 16px;
    position: absolute;
    right: -8px;
    bottom: -8px;
    font-family: title;
    color: white;
    text-shadow:
      0 0 3px #888,
      0 0 3px #888,
      0 0 3px #888,
      0 0 3px #888,
      0 0 3px #888,
      0 0 3px #888,
      0 0 3px #888;
  }
  .collection > div:hover {
    background-color: rgb(233 113 113);
    transform: scale(1.1);
  }
  .spellcard {
    width: 110px;
    height: 110px;
    border-radius: 12px;
    border: 4px solid white;
    cursor: pointer;
    margin: 5px;
    box-sizing: border-box;
    transition: 0.2s;
    position: relative;
  }
  .spellcard txt {
    font-size: 24px;
    position: absolute;
    right: -12px;
    bottom: -12px;
    font-family: title;
    color: white;
    text-shadow:
      0 0 4px #888,
      0 0 4px #888,
      0 0 4px #888,
      0 0 4px #888,
      0 0 4px #888,
      0 0 4px #888,
      0 0 4px #888;
  }
  .spellcard:hover {
    filter: drop-shadow(0 0 6px #666);
  }
  .other {
    flex-direction: row;
    height: 100px;
    margin-right: 50px;
  }
  .equipment,
  .souvenir {
    width: 80px;
    height: 100px;
    margin: 5px;
    align-items: center;
    background-color: #b896be;
    padding: 15px;
    box-sizing: border-box;
    border-radius: 40px 40px 0 0;
    filter: drop-shadow(0 0 4px #888);
    cursor: pointer;
  }
  .equipment > icon,
  .souvenir > icon {
    font-size: 50px;
  }
  .equipment > txt,
  .souvenir > txt {
    margin-top: 5px;
    text-align: center;
    color: white;
    font-family: normal;
  }
  .action {
    margin-top: 10px;
  }
</style>
