import info_spellcard from './spellcard.json';
import info_enermySpellcard from './enermySpellcard.json';
import info_buff from './buff.json';
import growth from '../data/growth';

const d = {};
Object.assign(d, info_spellcard);
Object.assign(d, info_enermySpellcard);

const info = {
  spellcard: d,
  buff: info_buff
}

export default {
  output: (data, Cache, spellcard) => {
    const output = {
      attack: [],
      buff: [],
      heal: []
    }
    if (data.role) data.state = growth.role[data.role](data.lv);
    const effect = deepCopy(info.spellcard[spellcard].output);
    spellcard == data.role && B("yanfan", _ => effect.attack = [{
      type: "scale",
      value: "35",
      aim: "one",
      amount: "4"
    }]);
    if ("buff" in effect) for (let buff of effect.buff) {
      output.buff.push(buff);
      if (buff.value == "Orrery's_Sun" && !("Orrery's_Sun" in Cache)) Cache["Orrery's_Sun"] = {
        state: data,
        attack: []
      };
    }
    if ("attack" in effect) {
      for (let attack of effect.attack) {
        attack.amount = attack.amount ? attack.amount : 1;
        if ("Orrery's_Sun" in data.buff) Cache["Orrery's_Sun"].attack.push(spellcard);
        else for (let a = 0; a < attack.amount; a++) output.attack.push(deepCopy(attack));
      }
    }
    if ("heal" in effect);

    // 附加区

    if ('collection' in data) {
      C("vampire's_old_tooth", amount => output.attack.length != 0 && output.attack.forEach(_ => output.heal.push({
        type: "value",
        value: 0.5 * amount
      })));
      C('portable_money_box', amount => output.attack.forEach(a => a.value = a.value * (1 + amount * 0.05 * Math.floor(data.coin / 100))));
      C('skyrocket', amount => spellcard == data.role && output.attack.forEach(a => {
        if (r() < 0.3) {
          a.value = a.value * 1.5;
          output.buff.push({
            value: 'burning',
            amount: amount,
            aim: 'one'
          });
        }
      }));
      C('pickled_radish', amount => data.health / data.state.health >= 0.95 && spellcard != data.role && output.attack.forEach(a => a.value *= 1 + (amount * 0.1)));
      C('bronze_mirror', amount => output.attack.forEach(a => r() < 0.25 && output.attack.push({
        type: a.type,
        value: a.value * 0.3 * amount,
        aim: 'random',
        critical: false,
        indirect: true
      })));
      C('earthy_spider_wine', amount => {
        if (spellcard == data.role) data.buff['strength_of_wine'] = amount;
      });
      C('amulet_of_full_moon', amount => {
        let i = Object.keys(data.buff).reduce((a, c) => a + (info.buff[c].positive == true ? 0 : 1), 0);
        i > 2 && output.attack.forEach(a => a.value *= 1 + (amount * 0.1));
      });
      C("compass_cat", (amount) => {
        data.speedGap < 0 && output.attack.forEach(a => a.value *= 1 + (amount * 0.1))
      });

      B('strength_of_wine', amount => spellcard != data.role && output.attack.forEach(a => a.value *= 1 + (amount * 0.1)));
      B('seal_needle', amount => {
        if (amount <= 0 && output.attack.length != 0) {
          C('seal_needle', a => output.buff.push({
            value: "bleed",
            amount: a,
            aim: output.attack.map(a => a.aim).includes("all") ? "all" : "one"
          }));
          data.buff['seal_needle'] += 9;
        }
      });
      B("Bright_Bitter_Wheel", _ => {
        output.attack.forEach(a => output.attack.push({
          type: a.type,
          value: a.value * 0.6,
          aim: 'one',
          indirect: true
        }));
      });
      spellcard == data.role && B('flow', _ => output.attack.forEach(a => a.value = a.value * 1.8));
      B("meditation_slash", _ => spellcard == data.role && output.attack.forEach(a => a.value = a.value * 3));
    }

    // 附加区

    output.attack.forEach(a => {
      a.from = data.role ? 'role' : 'enermy';
    });
    output.buff.forEach(b => b.amount = Number(b.amount));
    return output;
    function C(n, f) {
      if (n in data.collection) f(data.collection[n]);
    }
    function B(n, f) {
      if (n in data.buff) {
        let a = data.buff[n];
        !info.buff[n].positive && C('fish_stone', amount => a -= amount);
        f(a >= 0 ? a : 0);
        if (info.buff[n].expend) data.buff[n] == 1 ? delete data.buff[n] : data.buff[n]--;
      }
    }
  },
  input: (data, input) => { }
}

function r() {
  return Math.random();
}