const modes = ['chain', 'suit', 'random', 'priority'];

const model = {
  cirno: {
    stage1: {
      mode: 'random'
    }
  },
  piece: {
    stage1: {
      mode: 'random'
    }
  },
  lilywhite: {
    stage1: {
      mode: 'random'
    }
  },
  sunny: {
    stage1: {
      mode: 'random'
    }
  },
  lunar: {
    stage1: {
      mode: 'random'
    }
  },
  star: {
    stage1: {
      mode: 'random'
    }
  },
  eternity: {
    stage1: {
      mode: 'random',
    }
  }
}

export default function ai(lv, enermy) {
  let stage = Math.floor(lv / 16) + 1;
  model[enermy][stage]
  return enermy == 'cirno' ? ["cirno", "cirno", "cirno"] : ["Surprise_Spring", "Surprise_Spring"]
}