<script>
  import { fade, scale } from "svelte/transition";
  import { page, ctrl } from "../store";
  import words from "../data/words.json";

  let doodle = false;
  let pressed = false;
  let word = true;

  let txt1 = random();
  let txt2 = random();
  let txt3 = random();
  let txt4 = random();
  let txt5 = random();
  let txt6 = random();
  let txt7 = random();
  let txt8 = random();
  let txt9 = random();
  let txt0 = random();
  setTimeout((_) => setInterval((_) => (txt1 = random()), 2000), 200 * 0);
  setTimeout((_) => setInterval((_) => (txt2 = random()), 2000), 200 * 1);
  setTimeout((_) => setInterval((_) => (txt3 = random()), 2000), 200 * 2);
  setTimeout((_) => setInterval((_) => (txt4 = random()), 2000), 200 * 3);
  setTimeout((_) => setInterval((_) => (txt5 = random()), 2000), 200 * 4);
  setTimeout((_) => setInterval((_) => (txt6 = random()), 2000), 200 * 5);
  setTimeout((_) => setInterval((_) => (txt7 = random()), 2000), 200 * 6);
  setTimeout((_) => setInterval((_) => (txt8 = random()), 2000), 200 * 7);
  setTimeout((_) => setInterval((_) => (txt9 = random()), 2000), 200 * 8);
  setTimeout((_) => setInterval((_) => (txt0 = random()), 2000), 200 * 9);

  function random() {
    return {
      x: `${-40 - 160 * r()}px`,
      y: `${80 - 160 * r()}px`,
      word: words[Math.floor(r() * words.length)],
    };
  }
  function r() {
    return Math.random();
  }
</script>

<div
  class="body"
  on:click|once={(_) => (pressed = true)}
  in:fade={{ duration: 250 }}
  out:fade={{ duration: 250 }}
>
  {#if !pressed}
    {#if word}
      <div class="words" out:fade={{ duration: 250 }}>
        {#each [txt1, txt2, txt3, txt4, txt5, txt6, txt7, txt8, txt9, txt0] as p, i (i)}
          <txt
            style="left:{p.x};top:{p.y};font-size:{26 -
              r() * 8}px;animation-delay:{i * 0.2}s"
          >
            {p.word}
          </txt>
        {/each}
      </div>
    {:else}
      <txt class="launch" out:fade={{ duration: 250 }}>Press Any Key</txt>
    {/if}
  {/if}
  {#if doodle}
    <div class="title">
      <css-doodle>
        <style>
          :doodle {
            @grid: 13 / 100vmax;
            @min-size: 800px;
          }
          :container {
            transform: rotateY(30deg) rotate(10deg) scale(1.5);
          }
          :after {
            content: "";
            @size: 61.8%;
            background-size: @rand (5%, 50%) @rand (5%, 50%);
            background-position: center;
          }
          @even {
            background: #df0054;
            :after {
              background-image: linear-gradient(
                0deg,
                #f7f1e7 50%,
                transparent 50%
              );
            }
          }
          @odd {
            background: @pick (#f7f1e7, #10004a);
            :after {
              background-image: linear-gradient(
                90deg,
                #df0054 50%,
                transparent 50%
              );
            }
          }
        </style>
      </css-doodle>
    </div>
  {:else}
    <txt class="title">幻想异闻录</txt>
  {/if}
  {#if pressed}
    <div class="menu" in:fade={{ delay: 250, duration: 250 }}>
      <txt on:click={(_) => ($page = "Foreword")}>开始</txt>
      <txt on:click={(_) => ($page = "Afflatus")}>灵感</txt>
      <txt>设定</txt>
      <txt>离去</txt>
    </div>
  {/if}
</div>

<style>
  .menu {
    position: absolute;
    bottom: 20%;
    font-size: 30px;
    color: white;
    font-family: normal;
    line-height: 50px;
    text-shadow: 0 0 2px #888, 0 0 2px #888, 0 0 2px #888, 0 0 2px #888,
      0 0 2px #888, 0 0 2px #888, 0 0 2px #888;
  }

  .menu > txt {
    cursor: pointer;
    transition: 0.2s;
    opacity: 0.6;
  }

  .menu > txt:hover {
    transform: scale(1.1);
    opacity: 1;
  }

  .menu > txt:active {
    text-shadow: 0 0 3px #fff, 0 0 3px #fff, 0 0 3px #fff, 0 0 3px #fff,
      0 0 3px #fff, 0 0 3px #fff, 0 0 3px #fff;
  }

  .body {
    align-items: center;
    background-image: url("/img/scene/index.webp");
  }

  .launch {
    color: white;
    font-size: 24px;
    position: absolute;
    bottom: 32%;
    font-family: normal;
    animation: press 2s infinite;
  }

  div.title {
    mask-image: url("/img/title.webp");
    mask-size: 1000px;
    mask-position: center 20%;
    mask-repeat: no-repeat;
    -webkit-mask-image: url("/img/title.webp");
    -webkit-mask-size: 1000px;
    -webkit-mask-position: center 20%;
    -webkit-mask-repeat: no-repeat;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
  }

  txt.title {
    font-size: 150px;
    position: absolute;
    top: 20%;
    font-family: title;
    color: #444;
    text-shadow: 0 0 6px white, 0 0 6px white, 0 0 6px white, 0 0 6px white,
      0 0 6px white, 0 0 6px white, 0 0 6px white;
  }

  .words {
    position: absolute;
    bottom: 36%;
    align-items: center;
    justify-content: center;
  }
  .words txt {
    opacity: 0;
    color: white;
    font-size: 24px;
    position: absolute;
    width: 240px;
    text-align: center;
    animation: word 2s infinite;
    text-shadow: 0 0 1px #666, 0 0 1px #666, 0 0 1px #666, 0 0 1px #666,
      0 0 1px #666, 0 0 1px #666, 0 0 1px #666;
  }

  @keyframes word {
    0% {
      opacity: 0;
    }

    50% {
      opacity: 1;
    }

    100% {
      opacity: 0;
    }
  }
  @keyframes press {
    0% {
      opacity: 0;
    }

    50% {
      opacity: 1;
    }

    100% {
      opacity: 0;
    }
  }
</style>
