<script>
  import { onMount, afterUpdate } from "svelte";
  import { fade, scale } from "svelte/transition";
  import { Explain } from "../store";
  import collection from "../data/collection.json";
  import equipment from "../data/equipment.json";
  import souvenir from "../data/souvenir.json";
  import spellcard from "../data/spellcard.json";
  import consumable from "../data/consumable.json";
  import basecard from "../data/basecard.json";
  import role from "../data/role.json";
  import story from "../data/story.json";
  import Card from "../addon/card.svelte";
  import growth from "../data/growth";

  let index = "spellcard";
  let selected = null;
  const info = {
    collection,
    equipment,
    souvenir,
    spellcard,
    role,
    consumable,
    basecard,
  };
  let explain;
  const sp = {};
  for (let i in role) sp[i] = 0;

  onMount((_) => {
    explain = $Explain;
  });

  afterUpdate((_) => {
    if (index == "spellcard")
      for (let i in sp) {
        sp[i].onmousedown = (d) => {
          let offset = sp[i].scrollLeft;
          sp[i].onmousemove = (m) => {
            sp[i].scrollLeft = offset + d.clientX - m.clientX;
          };
          sp[i].onmouseup = (_) => (sp[i].onmousemove = null);
          sp[i].onmouseleave = (_) => (sp[i].onmousemove = null);
        };
      }
  });

  function Index(i) {
    selected = null;
    index = i;
  }
</script>

<div class="body" in:fade={{ duration: 250 }} out:fade={{ duration: 250 }}>
  <div class="index">
    <txt
      class={index == "collection" && "selected"}
      on:click={(_) => Index("collection")}>收藏品</txt
    >
    <txt
      class={index == "souvenir" && "selected"}
      on:click={(_) => Index("souvenir")}>纪念品</txt
    >
    <txt
      class={index == "equipment" && "selected"}
      on:click={(_) => Index("equipment")}>装备</txt
    >
    <txt
      class={index == "spellcard" && "selected"}
      on:click={(_) => Index("spellcard")}>符卡</txt
    >
    <txt
      class={index == "basecard" && "selected"}
      on:click={(_) => Index("basecard")}>通式</txt
    >
    <txt
      class={index == "consumable" && "selected"}
      on:click={(_) => Index("consumable")}>消耗品</txt
    >
  </div>
  <div class="main">
    {#if index == "collection"}
      <div
        class="collections container"
        in:fade={{ duration: 200 }}
        out:fade={{ duration: 200 }}
      >
        {#each ["blue", "green", "red"] as s, ii (ii)}
          {#each Object.keys(collection).filter((u) => collection[u].type == s) as c, i (i)}
            <div
              class={selected == c && "selected"}
              on:click={(_) => (selected = c)}
              on:mouseenter={(e) => explain.show(e)}
              on:mousemove={(e) => explain.move(e, collection[c], s)}
              on:mouseleave={(_) => explain.hide()}
            >
              <icon class="icon-{c}"></icon>
            </div>
          {/each}
        {/each}
      </div>
    {/if}
    {#if index == "souvenir"}
      <div
        class="souvenirs container"
        in:fade={{ duration: 200 }}
        out:fade={{ duration: 200 }}
      >
        {#each Object.keys(souvenir) as s, i (i)}
          <div
            class={selected == s && "selected"}
            on:click={(_) => (selected = s)}
            on:mouseenter={(e) => explain.show(e)}
            on:mousemove={(e) => explain.move(e, souvenir[s], "purple")}
            on:mouseleave={(_) => explain.hide()}
          >
            <icon class="icon-{s}"></icon>
          </div>
        {/each}
      </div>
    {/if}
    {#if index == "equipment"}
      <div
        class="equipments container"
        in:fade={{ duration: 200 }}
        out:fade={{ duration: 200 }}
      >
        {#each Object.keys(equipment) as s, i (i)}
          <div
            class={selected == s && "selected"}
            on:click={(_) => (selected = s)}
            on:mouseenter={(e) => explain.show(e)}
            on:mousemove={(e) => explain.move(e, equipment[s], "gold")}
            on:mouseleave={(_) => explain.hide()}
          >
            <icon class="icon-{s}"></icon>
          </div>
        {/each}
      </div>
    {/if}
    {#if index == "consumable"}
      <div
        class="consumables container"
        in:fade={{ duration: 200 }}
        out:fade={{ duration: 200 }}
      >
        {#each Object.keys(consumable) as s, i (i)}
          <div
            class={selected == s && "selected"}
            on:click={(_) => (selected = s)}
            on:mouseenter={(e) => explain.show(e)}
            on:mousemove={(e) => explain.move(e, consumable[s], "blue")}
            on:mouseleave={(_) => explain.hide()}
          >
            <icon class="icon-{s}"></icon>
          </div>
        {/each}
      </div>
    {/if}
    {#if index == "spellcard"}
      <div
        class="spellcard container"
        in:fade={{ duration: 200 }}
        out:fade={{ duration: 200 }}
      >
        {#each Object.keys(role) as r, i (i)}
          <div>
            <img
              on:mouseenter={(e) => explain.show(e)}
              on:mousemove={(e) =>
                explain.move(
                  e,
                  {
                    name: role[r].name,
                    detail: role[r].talent,
                  },
                  "blue",
                )}
              on:mouseleave={(_) => explain.hide()}
              class="avatar"
              src="/img/role/dairi/{r}.webp"
            />
            <txt
              class="role"
              on:mouseenter={(e) => explain.show(e)}
              on:mousemove={(e) => explain.move(e, {
                name: role[r].name,
                detail: `${role[r].detail}\n灵力上限：${growth.role[r](1).power}`
              }, "blue")}
              on:mouseleave={(_) => explain.hide()}>{role[r].name}</txt
            >
            <div class="spellcards" bind:this={sp[r]}>
              <div>
                {#each Object.keys(spellcard).filter((s) => spellcard[s].role == r) as s, ii (ii)}
                  <Card key={s} />
                {/each}
              </div>
            </div>
          </div>
        {/each}
      </div>
    {/if}
    {#if index == "basecard"}
      <div class="basecard container">
        {#each Object.keys(basecard) as b, i (i)}
          <div><Card key={b} /></div>
        {/each}
      </div>
    {/if}
    {#if selected}
      <div
        class="detail container"
        in:fade={{ duration: 200 }}
        out:fade={{ duration: 200 }}
      >
        <icon class="icon-{selected}"></icon>
        <txt class="name">{info[index][selected].name}</txt>
        <txt class="effect">{info[index][selected].detail}</txt>
        <txt class="story">{story[index][selected]}</txt>
      </div>
    {/if}
  </div>
</div>

<style>
  .body {
    background-image: url("/img/scene/afflatus.webp");
    background-position: center bottom;
  }

  .index {
    width: 900px;
    flex-direction: row;
  }
  .index > txt {
    width: 140px;
    line-height: 40px;
    margin: 5px;
    background-color: #63636369;
    border: 3px solid #ddd;
    font-size: 20px;
    color: white;
    text-align: center;
    box-sizing: border-box;
    cursor: pointer;
    font-family: normal;
    transition: 0.2s;
  }
  .index > txt.selected,
  .index > txt:hover {
    background-color: #a1a1a169;
    border-color: white;
    transform: scale(1.05);
  }

  .main {
    margin-top: 5px;
    height: 560px;
    width: 900px;
    flex-direction: row;
    position: relative;
  }

  .container {
    overflow-x: hidden;
    overflow-y: auto;
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none; /* IE 10+ */
  }
  .container::-webkit-scrollbar {
    display: none; /* Chrome Safari */
  }

  .spellcard {
    width: 900px;
    height: 560px;
    z-index: 2;
    position: absolute;
    left: 0;
    top: 0;
    color: white;
  }
  .spellcard > div {
    flex-wrap: wrap;
    height: 210px;
  }
  .spellcard .avatar {
    width: 140px;
    height: 140px;
    padding: 12px;
    margin: 5px;
    background-color: rgba(0, 0, 0, 0.3);
    box-sizing: border-box;
    border: 4px solid #aaa;
  }
  .spellcard .role {
    background-color: rgba(0, 0, 0, 0.3);
    box-sizing: border-box;
    border: 4px solid #aaa;
    width: 140px;
    height: 50px;
    font-size: 20px;
    margin: 5px;
    font-family: normal;
    line-height: 36px;
    text-align: center;
  }
  .spellcards {
    width: 740px;
    height: 210px;
    border: 4px solid #aaa;
    background-color: rgba(0, 0, 0, 0.3);
    box-sizing: border-box;
    margin: 5px;
    justify-content: center;
    overflow: hidden;
    position: relative;
  }
  .spellcards > div {
    position: absolute;
    flex-direction: row;
    height: 200px;
    align-items: center;
  }

  .basecard,
  .consumables,
  .equipments,
  .souvenirs,
  .collections {
    flex-direction: row;
    flex-wrap: wrap;
    width: 600px;
    height: 560px;
    align-content: flex-start;
    z-index: 2;
    position: absolute;
    left: 0;
    top: 0;
  }

  .basecard {
    width: 900px;
    justify-content: center;
  }
  .basecard > div {
    margin: 5px 1px;
  }

  .main > div:not(.spellcard):not(.basecard) > div {
    margin: 5px;
    background-color: rgba(0, 0, 0, 0.3);
    box-sizing: border-box;
    cursor: pointer;
    transition: 0.2s;
  }
  .main > div > div.selected,
  .main > div:not(.spellcard):not(.basecard) > div:hover {
    border-color: white;
    transform: scale(1.1);
  }
  .main > div > div > img {
    margin: auto;
    width: 75%;
    height: 75%;
  }

  icon {
    color: white;
    margin: auto;
  }
  .consumables > div,
  .collections > div {
    width: 50px;
    height: 50px;
    border: 3px solid #aaa;
    font-size: 34px;
  }
  .equipments > div,
  .souvenirs > div {
    width: 90px;
    height: 90px;
    border: 4px solid #aaa;
    font-size: 61px;
  }

  .detail {
    position: absolute;
    right: 0;
    top: 0;
    width: 290px;
    height: 560px;
    background-color: rgba(102, 102, 102, 0.3);
    backdrop-filter: blur(16px);
    box-sizing: border-box;
    border: 4px solid #aaa;
    margin: 5px;
    align-items: center;
    text-shadow:
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666;
  }
  .detail > icon {
    height: 100px;
    font-size: 100px;
    padding: 10px 20px;
    margin: 0;
    border-bottom: 2px solid #a5a5a5;
  }
  .detail > .name {
    font-size: 24px;
    color: white;
    margin: 10px;
    font-family: title;
  }
  .detail > .effect {
    width: 240px;
    text-align: center;
    font-size: 16px;
    color: white;
    font-family: remark;
  }
  .detail > .story {
    font-family: normal;
    width: 240px;
    padding: 20px;
    font-size: 16px;
    color: #dedede;
    white-space: pre-wrap;
  }
</style>
