export default {
  role: {
    marisa: lv => {
      return role(lv, 0.9, 0.9, 1, 1);
    },
    reimu: lv => {
      return role(lv, 1.2, 1.2, 1, 6);
    },
    shion: lv => {
      return role(lv, 1.1, 1.1, 1, 5);
    },
    medicine: lv => {
      return role(lv, 0.9, 0.9, 1, 4);
    },
    aya: lv => {
      return role(lv, 1, 1, 1.2, 5);
    },
    renko: lv => {
      return role(lv, 1, 1, 1, 5);
    },
    youmu: lv => {
      return role(lv, 0.8, 1.1, 1.1, 8);
    },
    pachi: lv => {
      return role(lv, 0.8, 0.8, 1.1, 8);
    },
    alice: lv => {
      return role(lv, 0.7, 1, 1, 4);
    }
  },
  enermy: {
    cirno: lv => {
      return enermy(lv, 1, 1, 1);
    },
    piece: lv => {
      return enermy(lv, 1, 1, 1);
    },
    lilywhite: lv => {
      return enermy(lv, 1, 1, 1);
    },
    sunny: lv => {
      return enermy(lv, 1, 1, 1);
    },
    lunar: lv => {
      return enermy(lv, 1, 1, 1);
    },
    star: lv => {
      return enermy(lv, 1, 1, 1);
    },
    eternity: lv => {
      return enermy(lv, 1, 1, 1);
    }
  },
  amount: lv => {
    let e = 4.2 - 1 / Math.pow(2, (lv / 32 - 2)),
      p = e - parseInt(e),
      a = Number(Math.random() < p);
    return parseInt(e) + a;
  }
}

function role(lv, h, a, s, p) {
  return {
    health: Math.ceil(r(lv) * h),
    attack: Math.ceil(r(lv) * a / 6),
    speed: 1 + 0.02 * lv * s,
    power: p
  }
}

function enermy(lv, h, a, s) {
  return {
    health: float(e(lv) * h),
    attack: float(e(lv) * a / 8),
    speed: 1 + 0.02 * (lv - 1) * s,
  }
}

function r(lv) {
  return parseInt(Math.pow(Math.E, lv / (Math.E * Math.log(lv + 1) + Math.sqrt(lv) / Math.E)) + lv + 10);
}

function e(lv) {
  return parseInt(Math.pow(Math.E, lv / (Math.log(lv + 1) + Math.sqrt(lv))) + lv + 10);
}

function float(v) {
  return v * 0.95 + Math.random().toFixed(2) * v * 0.1;
}