  }
  :global(.alice.spellcard) {
    --name: #777;
    --color1: rgb(255 239 186);
    --color2: rgb(151 150 204);
    --color3: rgb(255, 211, 246);
    --color4: rgb(150, 184, 204);
    background-color: rgb(242 142 146);
    background-image: /* Pink lines */
      linear-gradient(
        -116deg,
        transparent 40%,
        var(--color3) 0,
        var(--color3) 42%,
        transparent 42%
      ),
      linear-gradient(
        116deg,
        transparent 41%,
        var(--color3) 0,
        var(--color3) 43%,
        transparent 43%
      ),
      /* Black lines */
        linear-gradient(
          -116deg,
          transparent 40%,
          var(--color4) 0,
          var(--color4) 42%,
          transparent 42%
        ),
      linear-gradient(
        116deg,
        transparent 41%,
        var(--color4) 41%,
        var(--color4) 43%,
        transparent 43%
      ),
      /* Black diamonds */
