      8em 8em,
      8em 8em,
      8em 8em;
    background-position: /* Pink lines */
      3em -8em,
      -3em -8em,
      /* Black Lines */ -9em 8em,
      9em 8em,
      /* Black diamonds */ 0,
      0,
      0,
      0,
      /* Pink diamonds */ 4em,
      4em,
      4em,
