<script>
  import { onMount } from "svelte";
  import info from "../data/spellcard.json";
  import { data, cache } from "../store";
  import sector from "../data/sector";

  const Data = deepCopy($data);
  let onloadInfo = {
    limit: 4,
  };
  const spellcards = [];
  for (let s in Data.spellcards)
    for (let i = 0; i < Data.spellcards[s]; i++) spellcards.push(s);

  export let role;
  export let enermy;
  export let Selected;
  export const used = (_) => (useds = [...useds, selected]);
  export const refresh = add;

  let left;
  let hold = [];
  let selected;
  let useds = [];

  onMount((_) => {
    setTimeout((_) => {
      sector.spellcardOnload(
        Data,
        onloadInfo,
        role,
        enermy,
        $cache,
        spellcards,
      );
      left = leftReset();
    });
  });

  $: useThis = (i) => useds.includes(i);

  function add(value) {
    // 修改后的机制，用索引代表符卡，避免普攻被吞
    const info = {
      limit: onloadInfo.limit + value,
      ex: [],
    };
    sector.spellcardRefresh(Data, info, role, enermy, $cache, spellcards, hold);
    hold = hold.filter((i) => !useds.includes(i));
    left = left.filter((i) => !hold.includes(i)).rd();
    hold = [...hold, ...left.splice(0, info.limit - hold.length), ...info.ex];
    left = left.length == 0 ? leftReset() : left;
    useds = [];
    selected = null;
  }
  function leftReset() {
    let res = spellcards.map((_, i) => i);
    sector.spellcardLeftReset(Data, 0, role, enermy, $cache, res);
    return res;
  }
</script>

<div class="spellcards">
  {#each hold as i (i)}
    <div
      class={`spellcard ${
        selected == i && !useThis(i) && "selectedSpellcard"
      } ${useThis(i) && "animate_useSpellcard"} ${info[spellcards[i]].role}`}
      on:click={(_) => {
        selected = selected == i ? null : i;
        Selected = spellcards[selected];
      }}
    >
      <icon class="icon-{spellcards[i]}"></icon>
      <txt class="name">{info[spellcards[i]].name}</txt>
      <div class="detail">
        <txt>{info[spellcards[i]].detail}</txt>
      </div>
    </div>
  {/each}
</div>

<style>
  .spellcards {
    flex-direction: row;
    align-items: flex-end;
    height: 180px;
  }

  .spellcard {
    width: 125px;
    height: 180px;
    border-radius: 12px;
    border: 4px solid white;
    align-items: center;
    transition: 0.3s;
    filter: drop-shadow(0 0 6px #666);
    cursor: pointer;
    margin: 0px 7px;
    animation: animate_spellcard 0.3s;
    color: white;
  }
  .spellcard:hover {
    filter: drop-shadow(0 0 6px #fff);
  }
  .spellcard icon {
    font-size: 85px;
    margin-top: 8px;
  }
  .spellcard .name {
    background-color: white;
    color: #000;
    border-radius: 50%;
    padding: 4px 10px;
    mix-blend-mode: screen;
    font-size: 14px;
    line-height: 14px;
    margin: 3px;
  }
  .spellcard .name:before {
    content: "";
    width: 110px;
    height: 1px;
    background: white;
    position: absolute;
    top: 107px;
    left: 7px;
    z-index: -1;
  }
  .spellcard .detail {
    align-items: center;
    justify-content: center;
    width: 108px;
    height: 48px;
    font-size: 12px;
    text-align: center;
  }

  .selectedSpellcard {
    transform: translateY(-20px);
    z-index: 2;
  }
  .animate_useSpellcard {
    animation: useSpellcard 0.4s forwards;
  }

  @keyframes animate_spellcard {
    0% {
      opacity: 0;
      transform: translateY(120px);
    }

    100% {
      opacity: 1;
      transform: translateY(0px);
    }
  }
  @keyframes useSpellcard {
    0% {
      transform: translateY(-20px);
    }
    50% {
      transform: rotateY(90deg) translateY(-20px);
    }

    100% {
      transform: rotateY(90deg) translateY(-20px);
      width: 0;
      margin: 0;
      border: 0;
    }
  }
</style>
