<script>
  import { onMount } from "svelte";
  import { fade, scale } from "svelte/transition";
  import { data, cache, setting, page, explore, frameEvent } from "../store";
  import Spellcard from "./battleSpellcard.svelte";
  import Enermy from "./battleEnermy.svelte";
  import State from "./battleState.svelte";
  import Record from "./battleRecord.svelte";
  import Selector from "../addon/selector.svelte";
  import buffs from "../data/buff.json";
  import equipment from "../data/equipment.json";
  import _enermy from "../data/enermy.json";
  import growth from "../data/growth";
  import sector from "../data/sector";

  const Data = deepCopy($data);
  const raf = new window.RAF();
  let used;
  let Selected;
  let refresh;
  let ordered;
  let round = true;
  let role;
  let enermy;
  let record;
  let result;
  let selector;
  let level = "blue";
  let onloadInfo = {
    equipmentLimit: 1,
  };

  const battleWorker = {
    task: {
      roleEffect: {
        list: [],
        handle: (e) => role.input(e.content),
        add: function (e) {
          this.list.push({
            content: e,
            time: 25,
          });
        },
      },
      enermyEffect: {
        list: [
          {
            content: {
              // attack: [
              //   {
              //     type: "scale",
              //     value: 100,
              //     aim: "one",
              //     amount: "2",
              //     from: "role",
              //   },
              //   {
              //     type: "scale",
              //     value: 100,
              //     aim: "one",
              //     amount: "2",
              //     from: "role",
              //   },
              // ],
              attack: [],
              buff: [],
              heal: [],
            },
            target: 0,
            time: 25,
          },
        ],
        handle: (e) => enermy.input(e.content, e.target),
        add: function (e) {
          this.list.push({
            content: e,
            time: 25,
          });
        },
      },
    },
    state: false,
    handle: function () {
      let State = 0;
      for (let type in this.task) {
        if (this.task[type].list.length > 0) {
          if (!this.state) {
            this.task[type].handle(this.task[type].list.shift());
            this.state = true;
          } else {
            if (this.task[type].list[0].time == 1)
              this.task[type].handle(this.task[type].list.shift());
            else this.task[type].list[0].time--;
          }
        } else State++;
      }
      if (State == 0) this.state = false;
      enermy.render.handle();
    },
  };

  onMount((_) => {
    sector.battleOnload(Data, onloadInfo, role, enermy, $cache);
    setTimeout((_) => refresh(speedHandle()));
    $frameEvent.add("battleWorker", (_) => battleWorker.handle(), 1);
  });

  function aimed(index) {
    const info = {
      cost: Cost(Selected),
    };
    if (!round) msg({ content: "不在你的回合" });
    else if (info.cost > role.power.get()) msg({ content: "灵力不足" });
    else {
      used();
      role.power.cost(info.cost);
      let RoleInput = enermy.input(role.output(Selected), index);
      sector.afterPowerCost(Data, info, role, enermy, $cache, RoleInput);
      role.input(RoleInput, index);
      Selected = null;
    }
  }
  function Cost(card) {
    const info = {
      card: card,
      cost: card == Data.role ? 1 : 3,
    };
    sector.beforePowerCost(Data, info, role, enermy, $cache);
    return info.cost;
  }
  function switchRound() {
    switchEvent();
    // 续个回合
    round = false;
    record.clear();
    const unit = 200,
      afterEnd = 2,
      beforeStart = 4,
      enermyInterval = 6,
      spellcardInterval = 6,
      afterSwitch = 2,
      originOutput = enermy.output(),
      EnermyOutput = deepCopy(originOutput);
    const process = [
      {
        handle: false,
        time: afterEnd,
      },
    ];
    let step = raf.setInterval((_) => {
      process[0].time -= 1;
      if (process[0].time == 0) {
        process[0].handle && process[0].handle();
        const target = EnermyOutput.filter((e) => e),
          index = EnermyOutput.length - target.length;
        if (result) raf.clearInterval(step);
        else if (target.length == 0) {
          raf.clearInterval(step);
          raf.setTimeout((_) => {
            ordered = null;
            round = true;
          }, afterSwitch * unit);
          raf.setTimeout((_) => {
            role.round(roundEvent);
            refresh(speedHandle());
          }, beforeStart * unit);
        } else if (target[0].length == 0)
          process.push({
            handle: (_) => (EnermyOutput[index] = false),
            time: enermyInterval,
          });
        else {
          if (target[0].length == originOutput[index].length)
            raf.setTimeout((_) => {
              ordered = index;
              enermy.round(roundEvent, index);
            }, afterSwitch * unit);
          process.push({
            handle: (_) => {
              if (enermy.state()[index].Health <= 0)
                EnermyOutput[index] = false;
              else {
                enermy.input(role.input(target[0][0].output, index), index);
                record.add({
                  enermy: index,
                  spellcard: target[0][0].spellcard,
                });
                target[0].shift();
              }
            },
            time: enermy.state()[index].Health > 0 ? spellcardInterval : 1,
          });
        }
        process.shift();
      }
    }, unit);
  }
  function switchEvent() {
    let input = {
      buff: [],
      attack: [],
      heal: [],
    };
    C("ghost_lantern", (amount) =>
      input.buff.push({
        value: "interim_barrier",
        amount:
          Math.ceil(role.state.get().health * 0.05 * amount) * Power.get(),
      }),
    );
    role.input(input);
  }
  function roundEvent(Datas) {
    const input = {
      heal: [],
      attack: [],
      buff: [],
    };
    let p = Datas.state.power;
    let hypodynamic;
    B(Datas, "in_treatment", (amount) =>
      input.heal.push({
        type: "Rate",
        value: 0.05 * amount,
      }),
    );
    B(Datas, "burning", (amount) =>
      input.attack.push({
        value: 0.02 * amount,
        type: "rate",
        indirect: true,
        critical: false,
        aim: "one",
      }),
    );
    B(Datas, "bleed", (amount) =>
      input.attack.push({
        value: 0.02 * amount,
        type: "rate",
        indirect: true,
        critical: false,
        aim: "one",
      }),
    );
    B(Datas, "hypodynamic", (_) => (hypodynamic = 1));
    B(Datas, "star", (_) => {
      let target = $cache["bottle_of_stars"].filter(
        (s) => s.index == Datas.index,
      );
      target.forEach((t) =>
        input.attack.push({
          type: "real",
          value: t.damage,
          aim: "one",
          from: "role",
          critical: false,
        }),
      );
      $cache["bottle_of_stars"] = $cache["bottle_of_stars"].filter(
        (s) => s.index != Datas.index,
      );
    });
    if (input.heal.length > 0 || input.attack.length > 0) Datas.input(input);
    for (let b in Datas.buff) buffs[b].interim && delete Datas.buff[b];
    !hypodynamic && Datas.power(p);
    if (p) {
      !hypodynamic && Data.role == "marisa" && role.power.add(4);
      S("wanderlust", (_) =>
        role.power.add(Math.floor(role.state.get().speed)),
      );
      B(Datas, "Orrery's_Sun", (amount) => {
        delete Datas.buff["Orrery's_Sun"];
        let input = {
          attack: [],
          heal: [],
          buff: [],
        };
        for (let i = 0; i < amount; i++)
          "Orrery's_Sun" in $cache &&
            $cache["Orrery's_Sun"].attack.forEach((s) => {
              let o = role.output(s, $cache["Orrery's_Sun"].state);
              o.attack.forEach((u) => {
                u.indirect = true;
                input.attack.push(u);
              });
            });
        role.input(enermy.input(input));
        delete $cache["Orrery's_Sun"];
      });
    }
  }
  function killEvent(index) {
    const output = {
      attack: [],
      heal: [],
      buff: [],
    };
    C("bird_wine", (amount) =>
      output.buff.push({
        value: "burning",
        amount: amount,
        aim: "all",
      }),
    );
    enermy.input(output);
    C("crown_of_thorns", (amount) => role.power.add(amount));
    if (enermy.state().filter((e) => e.Health > 0).length <= 1) {
      $data.health = Math.ceil(role.state.get().Health);
      $data.lv++;
      $explore.lv++;
      $explore.enermy = $explore.enermy.filter((_, i) => !$explore.target[i]);
      $cache = {};
      $explore.target
        .map((t, i) => t && $explore.enermy[i])
        .filter((e) => e)
        .forEach((e) => {
          if (level != "red") if (_enermy[e].type == "elite") level = "green";
          if (_enermy[e].type == "red") level = "red";
        });
      selector("collection", level, (_) => ($page = "Explore"));
      raf.setTimeout((_) => {
        result = "win";
      }, 1200);
    }
  }
  function deadEvent(k) {
    result = "dead";
    if ($data.chance.amount > 0) {
      $data.chance.amount--;
      $data.lv++;
      $data.health = growth.role[$data.role]($data.lv).health;
      level = $data.chance.type;
      selector("collection", level, (_) => ($page = "Explore"));
    } else level = false;
  }
  function critical(from) {
    let base = 0.05;
    if (from == "role") {
      let state = role.state.get();
      C(
        "burning_seashore_flower",
        (amount) =>
          (base +=
            Math.floor(((state.health - state.Health) / state.health) * 100) *
            (0.005 + (amount - 1) * 0.0025)),
      );
      C("fluorescent_zanthoxylum", (amount) => (base += amount * 0.05));
    }
    return base;
  }
  function speedHandle() {
    let target = enermy.state().filter((e) => e.Health > 0);
    let adv = target.reduce((a, c) => a + c.speed, 0) / target.length;
    let value = Math.abs(role.state.get().speed - adv);
    let n = value - parseInt(value);
    let res = parseInt(value) + Number(r() < n);
    return role.state.get().speed > adv ? res : -res;
  }
  function Equipment() {
    if (!round) msg({ content: "不在你的回合" });
    else if (onloadInfo.equipmentLimit > 0) {
      const output = {
        attack: [],
        buff: [],
        heal: [],
      };
      let u = true;
      E("mini_bagua_stove", (_) => {
        output.attack.push({
          value: 400,
          type: "scale",
          aim: "all",
        });
      });
      E("louguanjian", (_) => {
        output.attack.push({
          value: 0.2,
          type: "Rate",
          aim: "all",
        });
      });
      E("hd_camera", (_) => {
        enermy.state().map((_, i) => enermy.buff.clear(i, true));
        $cache["bottle_of_stars"] = [];
      });
      E("sunshade", (_) => {
        let v = role.state.get().Health - role.state.get().health * 0.3;
        if (v > 0) {
          role.state.set("Health", v);
          output.buff.push({
            value: "barrier",
            amount: Math.ceil(role.state.get().health * 0.6),
            aim: "self",
          });
        } else {
          msg({ content: "不满足使用条件" });
          u = false;
        }
      });
      role.input(enermy.input(output));
      u && onloadInfo.equipmentLimit--;
    } else msg({ content: "可使用次数已用尽" });
  }
  function B(b, n, f) {
    if (n in b.buff) {
      let a = b.buff[n];
      b.role &&
        !buffs[n].positive &&
        C("fish_stone", (amount) => (a -= amount));
      f(a >= 0 ? a : 0);
      if (buffs[n].expend) b.buff[n] == 1 ? delete b.buff[n] : b.buff[n]--;
      if (n == "star") delete b.buff[n];
    }
  }
  function C(n, f) {
    if (n in Data.collection) f(Data.collection[n]);
  }
  function E(n, f) {
    if (Data.equipment == n) f();
  }
  function S(n, f) {
    if (Data.souvenir == n) f();
    return Data.souvenir == n;
  }
</script>

<div
  class="body {Selected} {result}"
  style="background-image:url('/img/scene/{Data.scene}.webp')"
  in:fade={{ duration: 250 }}
  out:fade={{ duration: 250 }}
>
  <div class="battle {result}">
    <Enermy
      {Selected}
      {aimed}
      {ordered}
      {critical}
      {killEvent}
      {role}
      bind:enermy
    />
    <Record bind:record />
    <Spellcard {role} {enermy} bind:used bind:Selected bind:refresh />
    <State {critical} {killEvent} {deadEvent} {enermy} {Cost} bind:role />
    <div class="action">
      <div class="consumables">
        <icon class="icon-consumables"></icon>
        <txt>消耗品</txt>
      </div>
      <div class="back" on:click={(_) => null}>
        <icon class="icon-back"></icon>
        <txt>回溯</txt>
      </div>
      <div class="equipment" on:click={(_) => Equipment()}>
        <icon class="icon-{Data.equipment ? Data.equipment : 'empty'}"></icon>
        <txt>{Data.equipment ? equipment[Data.equipment].name : "空"}</txt>
      </div>
      <div
        class={`end ${!round && "outRound"}`}
        on:click={(_) => round && switchRound()}
      >
        <txt>结束回合</txt>
      </div>
    </div>
  </div>
  <div
    class="barrier body"
    style="z-index:{result ? 5 : -1};{result && 'visibility:visible'}"
  >
    {#if result}
      <img src="/img/role/{$setting.resource}/{result}/{Data.role}.webp" />
    {/if}
    <div>
      <txt class="title">{result == "win" ? "胜利" : "满身疮痍"}</txt>
      {#if level}
        <Selector bind:selector />
        <div
          class="skip"
          on:click={(_) => {
            $page = "Explore";
            $data.coin += level == "blue" ? 100 : level == "green" ? 300 : 1500;
          }}
        >
          <txt
            >跳过并获得{level == "blue"
              ? 100
              : level == "green"
                ? 300
                : 1500}</txt
          >
          <icon class="icon-coin"></icon>
        </div>
      {:else}
        <txt class="skip" on:click={(_) => ($page = "Index")}>回到标题界面</txt>
      {/if}
    </div>
  </div>
</div>

<style>
  .battle {
    justify-content: space-between;
    align-items: center;
    height: 640px;
  }

  .barrier {
    flex-direction: row;
    visibility: hidden;
    transition: 0.4s;
    z-index: 7;
  }
  .barrier > img {
    height: 400px;
  }
  .barrier > div {
    justify-content: space-evenly;
    align-items: center;
    height: 320px;
  }
  .barrier > div > .title {
    text-align: center;
    color: white;
    font-size: 64px;
    font-family: "title";
    text-shadow:
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666;
  }
  .skip {
    flex-direction: row;
    justify-content: center;
    line-height: 30px;
    font-size: 24px;
    font-family: normal;
    text-shadow:
      0 0 2px #888,
      0 0 2px #888,
      0 0 2px #888,
      0 0 2px #888,
      0 0 2px #888,
      0 0 2px #888,
      0 0 2px #888;
    color: white;
    cursor: pointer;
    transition: 0.2s;
    opacity: 0.6;
  }
  .skip > icon {
    font-size: 30px;
    filter: drop-shadow(0 0 4px #333);
    transition: 0.2s;
  }
  .skip:hover {
    transform: scale(1.1);
    opacity: 1;
  }
  .skip:active {
    text-shadow:
      0 0 3px #fff,
      0 0 3px #fff,
      0 0 3px #fff,
      0 0 3px #fff,
      0 0 3px #fff,
      0 0 3px #fff,
      0 0 3px #fff;
  }
  .skip:active img {
    filter: drop-shadow(0 0 4px #fff);
  }
  .win .barrier {
    backdrop-filter: brightness(1.2) contrast(1.2) blur(4px);
  }
  .win .barrier > * {
    animation: result 0.4s forwards;
  }
  .dead .barrier {
    animation: dead 4s forwards;
  }

  :global(.battle-input) {
    position: absolute;
    color: white;
    font-size: 24px;
    width: 100px;
    text-align: center;
    animation: battleDamage 0.8s forwards;
    text-shadow:
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666,
      0 0 3px #666;
    z-index: 2;
  }
  :global(.battle-heal) {
    position: absolute;
    color: lime;
    font-size: 24px;
    width: 100px;
    text-align: center;
    animation: battleHeal 0.8s forwards;
    text-shadow:
      0 0 2px white,
      0 0 2px white,
      0 0 2px white,
      0 0 2px white,
      0 0 2px white,
      0 0 2px white,
      0 0 2px white;
    z-index: 2;
  }
  :global(.critical) {
    text-shadow:
      0 0 3px red,
      0 0 3px red,
      0 0 3px red,
      0 0 3px red,
      0 0 3px red,
      0 0 3px red,
      0 0 3px red;
  }

  .action {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 200px;
    height: 200px;
    border-radius: 200px 0 0 0;
    background-color: rgb(239 140 140);
    backdrop-filter: blur(8px);
  }
  .back,
  .equipment,
  .consumables {
    position: absolute;
    align-items: center;
    cursor: pointer;
    filter: brightness(0.9);
    opacity: 0.8;
    transition: 0.2s;
  }
  .back:hover,
  .equipment:hover,
  .consumables:hover {
    filter: brightness(1);
    opacity: 1;
  }
  .back > icon,
  .equipment > icon,
  .consumables > icon {
    font-size: 54px;
    color: white;
  }
  .back > txt,
  .equipment > txt,
  .consumables > txt {
    font-size: 16px;
    color: white;
    font-weight: bold;
    font-family: normal;
  }
  .consumables {
    right: 10px;
    top: 10px;
  }
  .back {
    left: 75px;
    top: 50px;
  }
  .equipment {
    bottom: 10px;
    left: 15px;
  }
  .end {
    position: absolute;
    right: 0;
    bottom: 0;
    background-color: white;
    background-image: url("/icon/outRound.svg");
    background-size: 60px;
    background-repeat: no-repeat;
    background-position: 100px 100px;
    border-radius: 100px 0 0 0;
    height: 100px;
    width: 100px;
    transition: 0.3s;
    cursor: pointer;
    overflow: hidden;
  }
  .end > txt {
    font-size: 24px;
    color: #777;
    position: absolute;
    right: 5px;
    bottom: 8px;
    width: 60px;
    letter-spacing: 5px;
    transition: 0.3s;
    font-family: title;
    line-height: 28px;
  }
  .end:hover {
    background-color: #eee;
  }
  .outRound {
    background-position: 30px 30px;
  }
  .outRound > txt {
    transform: translate3d(-70px, -70px, 0);
  }

  @keyframes battleDamage {
    0% {
      opacity: 0;
      transform: scale(0.6);
    }

    30% {
      opacity: 1;
      transform: scale(1.4);
    }

    40% {
      transform: scale(1);
    }

    90% {
      opacity: 1;
    }

    100% {
      opacity: 0;
    }
  }
  @keyframes battleHeal {
    0% {
      opacity: 0;
      transform: scale(0.6);
    }

    20% {
      opacity: 1;
      transform: scale(1);
    }

    50% {
      opacity: 1;
      transform: translateY(0px);
    }

    100% {
      opacity: 0;
      transform: translateY(-30px);
    }
  }
  @keyframes dead {
    0% {
      opacity: 0;
    }
    25% {
      opacity: 1;
    }
    100% {
      backdrop-filter: saturate(0) blur(4px);
    }
  }
  @keyframes result {
    0% {
      opacity: 0;
      transform: scale(0.6);
    }

    80% {
      opacity: 1;
      transform: scale(1.2);
    }

    100% {
      transform: scale(1);
    }
  }
</style>
