/**
 * @param {object} Data 简单来说，玩家面板
 * @param {object} info 独有的环境变量
 * @param {object} role 与角色有关的操作
 * @param {object} enermy 与敌人有关的操作
 */
export default {
  battleOnload: (Data, info, role, enermy, cache) => {
    /**
     * @todo 整场战斗初始化时
     * @param {number} equipmentLimit 本场战斗中装备的剩余使用次数
     */
    role.collection("philosopher's_stone", amount => info.equipmentLimit += amount);
  },
  roleOnload: (Data, info, role, enermy, cache) => {
    /**
     * @todo 角色信息初始化时
     */
    role.collection("patchouli's_ribbon", amount => {
      role.state.set("power", role.state.get().power + amount);
      role.power.reset();
    });
    role.collection("seal_needle", _ => role.buff.add("seal_needle", 8));
    role.souvenir("ahab's_opinion", _ => {
      let h = Math.floor(role.state.get().health * 0.7);
      role.state.set("health", h);
      role.state.set("Health", h);
    });
  },
  spellcardOnload: (Data, info, role, enermy, cache, spellcards) => {
    /**
     * @todo 手牌系统初始化时，在战斗中第一次抽牌之前
     * @param {number} limit 默认手牌上限，默认为4
     * @param {object} spellcards 自己的牌库
     */
    role.souvenir("wanderlust", (_) => info.limit = 3);
    role.collection("blade_of_yellow_spring", (amount) =>
      [...Array(amount).keys()].forEach((_) => spellcards.push(Data.role))
    );
  },
  spellcardRefresh: (Data, info, role, enermy, cache, spellcards, hold) => {
    /**
     * @todo 回合初刷新手牌时
     * @param {number} limit 经速度比较调整后的手牌上限，最终值
     * @param {object} spellcards 自己的牌库
     * @param {object} hold 显而易见，是当前手牌们
     */
    role.souvenir("wanderlust", (_) => (info.limit = 3));
    role.collection("blade_of_yellow_spring", (amount) => {
      info.ex = spellcards.map((_, i) => i).splice(-amount);
      hold.splice(-amount);
    });
    role.collection("blood_book", (amount) => (info.limit += amount));
  },
  spellcardLeftReset: (Data, info, role, enermy, cache, res) => {
    /**
     * @todo 牌堆被抽完的时候，将牌库洗进牌堆，在手牌系统初始化完成后也会发生一次，这次也是在第一次抽牌前
     * @param {object} res 牌库的原生复制，还未打乱顺序
     */
    role.collection("blade_of_yellow_spring", (amount) => res.splice(-amount));
  },
  beforePowerCost: (Data, info, role, enermy, cache) => {
    /**
     * @todo 打出卡时进行卡消耗的计算，在扣除之前
     * @param {string} card 卡的名字
     * @param {number} cost 卡的消耗，默认是1或3
     */
    if (role.buff.get("meditation_slash") && info.card == Data.role) info.cost++;
    role.equipment("mirror_of_pear", (_) => (info.cost = info.card == Data.role ? 0 : info.cost));
    role.souvenir("mimicry_cat_pendant", (_) => {
      console.log(role.state.get().Health * (1 - 0.1 * info.cost))
      role.state.set("Health", role.state.get().Health * (1 - 0.1 * info.cost))
      info.cost = 0;
    });
  },
  afterPowerCost: (Data, info, role, enermy, cache, RoleInput) => {
    /**
     * @todo 结算灵力之后
     * @param {object} RoleInput 角色输入
     * @param {number} cost 卡的最终消耗
     */
    if (Data.role == "youmu") RoleInput.buff.push({
      value: "telepathism",
      amount: info.cost,
    });
  }
}