<script>
  import { onMount } from "svelte";
  import { explore, data, cache, Explain } from "../store";
  import spellcard from "../data/spellcard.json";
  import Enermy from "../data/enermy.json";
  import buffs from "../data/buff.json";
  import growth from "../data/growth";
  import ai from "../data/ai";
  import calculate from "../data/calculate";
  import attackAnimation from "../addon/attackAnimation";

  export let Selected;
  export let state;
  export let ordered;
  export let aimed;
  export let critical;
  export let killEvent;
  const inputRender = {
    list: [],
    add: function () {},
    handle: function () {

    },
  }
  export const enermy = {
    round: round,
    output: output,
    input: inputHandle,
    render: inputRender,
    state: (_) => state,
    speedAdv: (_) => {
      let target = enermy.state().filter((e) => e.Health > 0);
      return (
        enermy
          .state()
          .filter((e) => e.Health > 0)
          .reduce((a, c) => a + c.speed, 0) / target.length
      );
    },
    buff: {
      get: (i, b) => {
        if (b) {
          if (b in enermy.state()[i].buff) {
            let a;
            B(i, b, (amount) => (a = amount));
            return a;
          } else return false;
        } else return enermy.state()[i].buff;
      },
      clear: (i, positive) => {
        if (positive === true)
          for (let b in state[i].buff)
            buffs[b].positive && delete state[i].buff[b];
        else if (positive === false)
          for (let b in state[i].buff)
            !buffs[b].positive && delete state[i].buff[b];
        else state[i].buff = {};
      },
    },
  };
  export let role;

  $: allAim = Selected == null ? false : "allAim" in spellcard[Selected];

  const Explore = deepCopy($explore);
  const Data = deepCopy($data);
  const enermys = Explore.target
    .map((t, i) => t && Explore.enermy[i])
    .filter((e) => e);
  state = enermys.map((e) => {
    let s = growth.enermy[e](Explore.lv);
    s.Health = s.health;
    s.buff = {};
    return s;
  });
  let input = null;
  let explain;

  onMount((_) => (explain = $Explain));

  function r() {
    return Math.random();
  }
  function inputHandle(Input, index) {
    let res = enermys.map((_) => {
      return {
        attack: [],
        buff: [],
        heal: [],
      };
    });
    let selfAim = {
      heal: [],
      attack: [],
      buff: [],
    };
    let e = [...Array(res.length).keys()];
    let aim = {
      one: [index],
      all: e,
      else: e.filter((i) => i != index),
      average: e,
      random: e
        .filter((i) => state[i].Health > 0)
        .rd()
        .splice(0, 1),
    };
    let roleState = role.state.get();
    for (let attack of Input.attack) {
      let damage = Number(attack.value);
      attack.amount = attack.amount ? attack.amount : 1;
      if (attack.type == "scale") damage *= roleState.attack * 0.01;
      if (attack.type == "real");
      if (attack.aim == "average")
        damage /= e.filter((i) => state[i].Health > 0).length;
      let att = deepCopy(attack);
      let Cri = critical(att.from);
      let Critical = Data.role == "youmu" ? 0 : Cri;
      att.critical = "critical" in att ? att.critical : r() < Critical;
      let criticalDamage = 1.5;
      if (att.from == "role") {
        C("human_soul_lamp", (amount) => (criticalDamage += amount * 0.5));
        if (Data.role == "youmu") criticalDamage += Cri;
      }
      att.from == "role" &&
        C("human_soul_lamp", (amount) => (criticalDamage += amount * 0.5));
      att.damage = damage * (att.critical ? criticalDamage : 1);
      if (attack.aim == "self") selfAim.attack.push(att);
      else
        for (let i of aim[attack.aim]) {
          let Att = deepCopy(att);
          if (role.buff.get("cursed_wind")) {
            enermy.buff.clear(i, true);
            Input.buff = Input.buff.filter((b) => b.aim == "self");
            $cache["bottle_of_stars"] = $cache["bottle_of_stars"].filter(
              (u) => u.index != i
            );
          }
          if (attack.type == "rate") Att.damage *= state[i].health;
          if (attack.type == "Rate") Att.damage *= state[i].Health;
          res[i].attack.push(Att);
        }
    }
    for (let heal of Input.heal) selfAim.heal.push(heal);
    for (let Buff of Input.buff) {
      if (Buff.aim == "self")
        selfAim.buff.push({
          value: Buff.value,
          amount: Buff.amount,
        });
      else for (let i of aim[Buff.aim]) res[i].buff.push(Buff);
    }
    let o = effectHandle(res);
    for (let i in o) o[i].forEach((u) => selfAim[i].push(u));
    return selfAim;
  }
  function effectHandle(Effect) {
    const res = {
      attack: [],
      buff: [],
      heal: [],
    };
    const effect = deepCopy(Effect);
    effect.forEach((e, i) => {
      for (let a of e.attack) {
        if (a.indirect) {
          a.from == "role" &&
            C("sundial", (amount) => (a.damage *= 1 + amount * 0.1));
        }
        E("sword_of_feixiang", (_) => {
          if (
            Object.keys(state[i].buff).reduce(
              (a, c) => (a + buffs[c].positive ? 0 : 1),
              0
            ) > 0
          )
            a.damage *= 1.4;
        });
        if (state[i].Health > 0 && state[i].Health - a.damage <= 0)
          killEvent(i);
        if (a.damage > 0 && a.from == "role") {
          //“造成伤害”事件
          role.buff.set("seal_needle", role.buff.get("seal_needle") - 1);
          C("bottle_of_stars", (amount) => {
            if (a.damage >= role.state.get().attack * 3) {
              e.buff.push({
                value: "star",
                amount: amount,
              });
              if (!$cache["bottle_of_stars"]) $cache["bottle_of_stars"] = [];
              for (let u = 0; u < amount; u++)
                $cache["bottle_of_stars"].push({
                  index: i,
                  damage: a.damage * 0.5,
                });
            }
          });
          C("cursed_wind_chimes", (amount) => {
            if (
              a.damage >=
              role.state.get().attack * 6 * Math.pow(0.95, amount - 1)
            )
              res.buff.push({
                value: "cursed_wind",
                amount: 1,
              });
          });
          E("gongonier", (_) => {
            if (a.critical)
              res.heal.push({
                type: "value",
                value: a.damage * 0.2,
              });
          });
        }
        state[i].Health -= a.damage;
      }
      for (let h of e.heal) {
        healthSet(h + state[i].Health, i);
        delete state[i].buff["bleed"];
      }
      for (let b of e.buff) {
        state[i].buff[b.value] = state[i].buff[b.value]
          ? state[i].buff[b.value] + b.amount
          : b.amount;
      }
    });
    input = effect;
    state = state;
    setTimeout((_) => (input = null), 800);
    return res;
  }
  function output() {
    const speeds = state.map((i) => i.speed),
      order = [];
    for (let i = 0; i < speeds.length; i++) {
      let index = speeds.indexOf(Math.max(...speeds));
      order.push(index);
      speeds[index] = 0;
    }
    return order.map((i) =>
      ai(Explore.lv, enermys[i]).map((n) => {
        return {
          output: calculate.output(state[i], (c) => ($cache = c), n),
          spellcard: n,
        };
      })
    );
  }
  function round(roundEvent, index) {
    roundEvent({
      health: state[index].Health,
      state: state[index],
      buff: state[index].buff,
      input: (i) => inputHandle(i, index),
      power: (_) => null,
      index: index,
    });
    state = state;
  }
  function healthSet(h, i) {
    state[i].Health = h > state[i].health ? state[i].health : h;
  }
  function B(i, n, f) {
    if (n in state[i].buff) {
      f(state[i].buff[n]);
      if (buffs[n].expend)
        state[i].buff[n] == 1 ? delete state[i].buff[n] : state[i].buff[n]--;
    }
  }
  function C(n, f) {
    if (n in Data.collection) f(Data.collection[n]);
  }
  function E(n, f) {
    if (Data.equipment == n) f();
  }
</script>

<div class="enermys {allAim && 'allAim'}">
  {#each enermys as e, i (i)}
    <div
      class={`enermy ${!allAim && Selected && "oneAim"} ${
        state[i].Health <= 0 && "animate_enermyOut"
      } ${ordered == i && "orderEnermy"}`}
      on:click={(_) => {
        if (state[i].Health > 0 && Selected) aimed(i);
      }}
      style="z-index:{enermys.length - i}"
    >
      <div class="enermy-info">
        <img
          src={`/img/enermy/${Enermy[e].type}/${e}/${
            (input && input[i].attack.length != 0) || state[i].Health <= 0
              ? "hurt"
              : "portrait"
          }.webp`}
          class={`${
            input && input[i].attack.length != 0 && "animate_enermyShake"
          }`}
        />
        <txt>{Enermy[e].name}</txt>
      </div>
      <div class="enermy-state">
        <div
          class="enermy-health"
          style="visibility:{state[i].Health / state[i].health != 1
            ? 'visible'
            : 'hidden'};background-image:{`linear-gradient(90deg, white ${
            (state[i].Health / state[i].health) * 100
          }%, transparent ${(state[i].Health / state[i].health) * 100}%)`}"
        />
        <div class="enermy-buff">
          {#each Object.keys(state[i].buff) as b, ii (ii)}
            <div
              on:mouseenter={(e) => explain.show(e)}
              on:mousemove={(e) =>
                explain.move(
                  e,
                  buffs[b],
                  buffs[b].positive ? "blue" : "purple"
                )}
              on:mouseleave={(_) => explain.hide()}
            >
              <icon class="icon-{b}"></icon>
              {#if Object.values(state[i].buff)[ii] > 1}
                <txt>{Object.values(state[i].buff)[ii]}</txt>
              {/if}
            </div>
          {/each}
        </div>
      </div>
      <div class="enermy-aim icon-aim" />
      <!-- {#if input && input[i].attack.length != 0}
        {#each input[i].attack as a, ii (ii)}
          <a
            class="battle-input {a.critical && 'critical'}"
            style="left:{-25 + r() * 150}px;top:{50 + r() * 150}px"
            >{a.damage.toFixed(1)}</a
          >
        {/each}
      {/if} -->

      {#if input && input[i].attack.length != 0}
        <svelte:component this={attackAnimation[Data.role]} />
      {/if}
    </div>
  {/each}
</div>

<style>
  .enermys {
    flex-direction: row;
  }

  .enermy-info {
    position: relative;
    margin: 5px;
    width: 200px;
    align-items: center;
    overflow: hidden;
  }

  .enermy-info > img {
    height: 250px;
  }

  .enermy-info > txt {
    color: white;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    text-align: center;
    text-shadow: 0 0 3px #666, 0 0 3px #666, 0 0 3px #666, 0 0 3px #666,
      0 0 3px #666, 0 0 3px #666, 0 0 3px #666;
    font-size: 20px;
    font-family: normal;
    font-weight: bold;
  }

  .enermy-state {
    margin-top: 5px;
    width: 100%;
    align-items: center;
  }

  .enermy-health {
    box-sizing: border-box;
    width: 150px;
    height: 12px;
    border: 4px solid white;
    filter: drop-shadow(0 0 6px #777);
  }

  .enermy-buff {
    flex-direction: row;
    width: 200px;
    justify-content: center;
    margin-top: 2px;
    font-size: 24px;
    color: white;
  }

  .enermy-buff > div {
    width: 24px;
    height: 24px;
    margin-right: 8px;
    position: relative;
    filter: drop-shadow(0 0 8px #555);
  }

  .enermy-buff > div > txt {
    position: absolute;
    right: -5px;
    bottom: -5px;
    font-size: 14px;
    color: white;
    text-shadow: 0 0 3px #777, 0 0 3px #777, 0 0 3px #777, 0 0 3px #777,
      0 0 3px #777, 0 0 3px #777, 0 0 3px #777;
    font-family: title;
  }

  .enermy-aim {
    opacity: 0;
    transition: 0.2s;
    font-size: 50px;
    text-align:center;
    color:white;
  }

  .oneAim:hover .enermy-aim,
  .allAim:hover .enermy-aim {
    opacity: 1;
  }

  .allAim > div,
  .oneAim {
    cursor: pointer;
  }

  .enermy {
    position: relative;
    background-image: radial-gradient(
      #ffffff91,
      #ffffff82,
      transparent,
      transparent
    );
    transition: 0.3s;
  }

  .orderEnermy {
    transform: translateY(20px);
  }

  .animate_enermyOut {
    animation: enermyOut 1.6s forwards;
  }

  .animate_enermyShake {
    animation: enermyShake 0.6s ease-in-out;
  }

  @keyframes enermyOut {
    0% {
      opacity: 1;
    }

    50% {
      opacity: 1;
    }

    100% {
      opacity: 0;
      cursor: auto;
    }
  }

  @keyframes enermyShake {
    10%,
    90% {
      transform: translate3d(-1px, 0, 0);
    }

    20%,
    80% {
      transform: translate3d(+2px, 0, 0);
    }

    30%,
    70% {
      transform: translate3d(-4px, 0, 0);
    }

    40%,
    60% {
      transform: translate3d(+4px, 0, 0);
    }

    50% {
      transform: translate3d(-4px, 0, 0);
    }
  }
</style>
